<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full required">
          <label class="vs-input--label">{{ $t('CURRENCY_NAME') }}</label>
          <vs-input class="w-full" v-model="currency.description"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('CURRENCY_CODE') }}</label>
          <vs-input class="w-full" v-model="currency.abbreviation"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('NUMERIC_CODE') }}</label>
          <vs-input class="w-full" v-model="currency.numericCode"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <vs-checkbox v-model="currency.disabled">{{ $t('DISABLED') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyCurrency('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyCurrency('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyCurrency('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import UomService from '@/services/commons/UomService'

export default {
  name: 'currency-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      currency: {}
    }
  },
  methods: {
    async load () {
      let self = this
      UomService.uoms({
        uomId: this.$route.params.id,
        uomTypeEnumId: 'UT_CURRENCY_MEASURE'
      }).then(function (response) {
        self.currency = response.data.uomList[0] || {}
        self.currency.disabled = self.currency.disabled === 'Y'
      })
    },
    modifyCurrency (nextAction) {
      let self = this
      UomService.modifyUom({
        uomId: this.$route.params.id,
        uomTypeEnumId: 'UT_CURRENCY_MEASURE',
        abbreviation: this.currency.abbreviation,
        description: this.currency.description,
        numericCode: this.currency.numericCode,
        disabled: this.currency.disabled ? 'Y' : 'N'
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewCurrency', params: { id: response.data.uomId } })
          } else {
            self.$router.push({ name: 'BrowseCurrencies' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
